.app {
  background-color: black;
  min-width: 100vw;
  min-height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.panel {
  position: absolute;
  bottom: 20px;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 100%;
  transition: .3s ease-in-out;
}

.panelHidden {
  composes: panel;
  opacity: 0;
  visibility: hidden;
}

.buttonsContainer {
  display: flex;
  gap: 10px;
}


.button {
  border: none;
  font-size: 20px;
  padding: 10px 20px;
  font-weight: bold;
  border-radius: 10px;
  color: black;
  transition: background-color .3s linear;
}

.buttonRed {
  composes: button;
  background-color: red;
}

.buttonBlue {
  composes: button;
  background-color: blue;
}

.buttonGreen {
  composes: button;
  background-color: green;
}